import { VueNodeConfig } from './VueNodeConfig'
import { Graph } from "@antv/x6"
import '@antv/x6-vue-shape'
import VueShapeNode from './vue-shape-node/vue-shape-node.vue'

Graph.registerVueComponent(
    "VueShapeNode",
    {
      template: `<vue-shape-node :id="null" />`,
      components: {
        VueShapeNode,
      },
    },
    true
)

// vue-shape-node下的data基本属性
export interface NodeData {
  mass: number,  // 质量
  power: number, // 热功耗
  comp_occupy_heatpipe_num: number,     // 横跨热管数量
  comp_occupy_heatpipe_options: number[], // 横跨热管的可能数量
  placedDomainNumber: number,            // 放置子区域
  angle: number,
  [x: string]: any
}
