import { GraphCore } from '@/graph-core/graph-core';
import { Component, Vue, Watch } from 'vue-property-decorator';
import { Node } from '@antv/x6';
import { Subscription } from 'rxjs/internal/Subscription';
import NodeProperty from './node-property/node-property.vue';


@Component({
  components: {
    NodeProperty
  },
})
export default class ConfigBar extends Vue {
  // 节点属性Drawer
  nodeConfigDrawer: boolean = false
  // 当前选中的节点
  selectedNode: Node<Node.Properties> | null = null
  // 订阅者数组
  subscribes: Subscription[] = []
  
  mounted() {
    // 监听选中节点变化
    this.subscribes.push(GraphCore.selectedNode$.subscribe(this.onSelectedNodeChange))
  }

  // 选中节点变化回调函数
  onSelectedNodeChange(node: Node<Node.Properties> | null) {
    this.selectedNode = node
    // 打开Drawer
    this.nodeConfigDrawer = !!this.selectedNode
  }

  // 节点属性Drawer关闭回调函数
  nodeConfigDrawerClose() {
    GraphCore.graph.unselect(this.selectedNode!)
  }

  // 组件销毁时取消所有订阅
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }

}
